import React from "react";
import styled from "styled-components";
import { Carousel } from "react-bootstrap";

const Image = styled.img`
  flex: 65%;
  border-radius: 10px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  @media (max-width: 768px) {
    flex: 100%;
  }
`;

const Slider = ({ photos }) => {
  return (
    <Carousel
      style={{
        width: "40%",
        // margin: "20px 0",
        marginLeft: "auto",
        marginRight: "40px",
        position: "relative",
        bottom: "110px",
      }}
    >
      {photos.map((img, index) => (
        <Carousel.Item key={index}>
          <Image className="d-block w-100" alt="Slide" src={img.default} />
        </Carousel.Item>
      ))}
    </Carousel>
  );
};

export default Slider;
