import React from "react";
import styled from "styled-components";
// import { NavLink } from "react-router-dom";

const StyledWrapper = styled.ul`
  height: 50px;
  flex: 80%;
  padding: 0;
  text-align: right;
  @media (max-width: 768px) {
    flex: 100%;
  }
`;

const StyledLi = styled.li`
  cursor: pointer;
  display: inline-block;
  min-width: 60px;
  font-family: "Poiret One", cursive;
  font-weight: 600;
  font-size: 20px;
  padding: 12px 13px;
  list-style: none;
  @media (max-width: 800px) {
    font-size: 15px;
    padding: 12px 5px;
    min-width: 10px;
  }
`;

const StyledLink = styled.a`
  text-decoration: none;
  color: black;
  :hover,
  &.active {
    text-decoration: none;
    color: white;
  }
`;

const Menu = () => {
  return (
    <StyledWrapper>
      <StyledLi>
        <StyledLink href="/home" activeClassName="active">
          Strona główna
        </StyledLink>
      </StyledLi>
      <StyledLi>
        <StyledLink href="/laser" activeClassName="active">
          Laser
        </StyledLink>
      </StyledLi>
      <StyledLi>
        <StyledLink href="/architektura-ogrodowa" activeClassName="active">
          Architektura ogrodowa
        </StyledLink>
      </StyledLi>
      <StyledLi>
        <StyledLink href="/dekoracje" activeClassName="active">
          Dekoracje
        </StyledLink>
      </StyledLi>
    </StyledWrapper>
  );
};

export default Menu;
